@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,900;1,300;1,600&display=swap");

:root {
  --bg-card: gray;
  --border-clr: #43cc47;
  --border-clr-alter: ;

  --transition: all 700ms ease-in-out 25ms;
}

body {
  /* margin: 1rem; */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  font-family: "Poppins", sans-serif;
  line-height: 1.7;
  position: relative;
  width: 100%;
  /* background: linear-gradient(90deg, white, rgba(128, 128, 128, 0.288), white); */
  animation: animBack 10s infinite;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

a {
  text-decoration: none;
  border-radius: 1rem;
  padding: 0.5rem;
  color: rgb(1, 240, 248);
  transition: var(--transition);
}

a:hover {
  color: rgb(250, 4, 229);
}

.hireMeBtn {
  position: fixed;
  right: 1rem;
  top: 1rem;

  border-color: transparent;
  background-color: rgba(0, 195, 255, 0.557);
  transition-duration: 0.4s;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 0.2rem;
  font-size: 1.5rem;
  color: white;
}

.hireMeBtn:hover {
  color: aquamarine;
  background-color: white;
  border-color: aquamarine;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
